.summoner-profile .summoner-matches .matches .match {
  min-height: 50px;
  margin: 20px 0px; }
  .summoner-profile .summoner-matches .matches .match .match-wrapper {
    display: flex; }
    .summoner-profile .summoner-matches .matches .match .match-wrapper .panel.items .items-wrapper {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 5px; }
      .summoner-profile .summoner-matches .matches .match .match-wrapper .panel.items .items-wrapper .item {
        background-color: rgba(0, 0, 0, 0.5);
        width: 40px; }
        .summoner-profile .summoner-matches .matches .match .match-wrapper .panel.items .items-wrapper .item img {
          width: 100%;
          height: 100%;
          display: block;
          object-fit: cover; }
  .summoner-profile .summoner-matches .matches .match.match-won {
    background-color: #073763;
    border-left: 5px solid #08a6ff; }
  .summoner-profile .summoner-matches .matches .match.match-lost {
    background-color: #45192b;
    border-left: 5px solid #ff4e50; }
